import { NavLink } from 'react-router-dom'
import './Information.css'
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import { AnimatePresence, motion } from 'framer-motion';
import { useSelector } from 'react-redux';
import { appStateType } from '../../../Redux/Store';

const htmlImg = require('../../../assets/htmlIcon.png')
const cssImg = require('../../../assets/cssIcon.png')
const jsImg = require('../../../assets/jsIcon.png')
const tsImg = require('../../../assets/tsIcon.png')
const reactImg = require('../../../assets/reactIcon.png')
const reduxImg = require('../../../assets/reduxIcon.png')

export const Information = () => {

  const isDarkTheme = useSelector((state: appStateType) => state.ThemeReducer.darkTheme)

  return (
    <AnimatePresence
      exitBeforeEnter
      initial={true}
    >
      <motion.div
        initial={{ y: 20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}        
        transition={{ duration: 0.65 }}
      >
        <div className="Information">
          <div className="aboutMe">
            <h2 className="aboutMe__title title"> </h2>
            <div className="aboutMe__content content">
              <p className="aboutMe__text text">Frontend .    HTML5()  CSS -  , Java Script - 1 , React  Redux - 7 , Type Script - 4 .   React Router DOM, React-Redux, Redux Thunk, Formik, Material UI.      Git.      .     Redux Toolkit, Node JS, Express JS, MongoDB. </p>
              <NavLink className={`${isDarkTheme ? "aboutMe__btn dark" : "aboutMe__btn"}`} to='/works'>
                 <ArrowRightIcon />
              </NavLink>
            </div>
          </div>
          <div className='skills'>
            <h2 className="skills__title title"></h2>
            <div className='skills__content content'>
              <ul className="skills__list">
                <li className="skills__li">
                  <img src={htmlImg} alt="" />
                  <span className='skills__name'>HTML5</span>
                </li>
                <li className="skills__li">
                  <img src={cssImg} alt="" />
                  <span className='skills__name'>CSS3</span>
                </li>
                <li className="skills__li">
                  <img src={jsImg} alt="" />
                  <span className='skills__name'>Java Script</span>
                </li>
                <li className="skills__li">
                  <img src={tsImg} alt="" />
                  <span className='skills__name'>Type Script</span>
                </li>
                <li className="skills__li">
                  <img src={reactImg} alt="" />
                  <span className='skills__name'>React</span>
                </li>
                <li className="skills__li">
                  <img src={reduxImg} alt="" />
                  <span className='skills__name'>Redux</span>
                </li>
              </ul>
            </div>
          </div>
          <div className="education">
            <h2 className="education__title title">Education</h2>
            <div className='education__content content'>
              <p className='education__subtitle'>-    (2017-2021.)</p>
              <p className='education__text'>:   </p>
            </div>
          </div>
        </div>
      </motion.div>
    </AnimatePresence >
  )
}
