import { inferActionsType } from "./Store";

let initialState = {
    darkTheme: false
}

type initialStateType = typeof initialState
const ThemeReducer = (state = initialState, action: actionsType): initialStateType => {
    switch (action.type) {

        case 'theme/TOGGLE_DARKTHEME': {
            return {
                ...state,
                darkTheme: action.isDarkTheme,
            };
        }

        default:
            return state;
    }
}

type actionsType = inferActionsType<typeof actions>
export const actions = {
    toggleDarkTheme: (isDarkTheme: boolean) => ({ type: 'theme/TOGGLE_DARKTHEME', isDarkTheme} as const),
}

export default ThemeReducer;
