import React from 'react'
import './Header.css'
import { Nav } from './Nav'
import Brightness4Icon from '@mui/icons-material/Brightness4'
import LightModeIcon from '@mui/icons-material/LightMode';
import { useDispatch, useSelector } from 'react-redux';
import { actions } from '../../Redux/ThemeReducer';
import { appStateType } from '../../Redux/Store';

type PropsType = {

}
export const Header: React.FC<PropsType> = (props) => {

  const isDarkTheme = useSelector((state: appStateType) => state.ThemeReducer.darkTheme)
  const dispatch = useDispatch()
  const onThemeChange = () => {
    if (isDarkTheme) {
      dispatch(actions.toggleDarkTheme(false))
    } else {
      dispatch(actions.toggleDarkTheme(true))
    }
  }
  return (
    <div className="header">
      <div className={`${isDarkTheme ? "header__wrapper dark" : "header__wrapper"}`}>
        <div className="header__logo">
          <img src="" alt="" />
        </div>
        <Nav />
        <button
          className={`${isDarkTheme ? "header__themeButton dark" : "header__themeButton"}`}
          onClick={onThemeChange}
        >
          {isDarkTheme ? <LightModeIcon /> : <Brightness4Icon/>}
        </button>
      </div>
    </div>
  )
}
