import React from 'react'
import { Information } from './Information/Information'
import './Content.css'
import { Route, Routes } from 'react-router-dom'
import { Works } from './Works/Works'

export const Content = () => {
    return (
        <div className="Content">
            <Routes>
                <Route path='/'
                    element={<Information />}
                />
                <Route path='/works'
                    element={<Works />}
                />
            </Routes>
        </div>
    )
}
