import { AnimatePresence, motion } from 'framer-motion'
import { useSelector } from 'react-redux'
import { appStateType } from '../../../Redux/Store'
import { WorksType } from '../../../Redux/WorksReducer'
import './Works.css'

type PropsType = {}
export const Works = (props: PropsType) => {
  const isDarkTheme = useSelector((state: appStateType) => state.ThemeReducer.darkTheme)
  const WorksData = useSelector((state: appStateType) => state.WorksReducer.WorksData)
  return (
    <AnimatePresence
      exitBeforeEnter
      initial={true}
    >
      <motion.div
        initial={{ y: 20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.65 }}>
        <div>
          {WorksData.map((data: WorksType) => <div key={data.link}>
            <h2 className={`${isDarkTheme ? 'Works__title dark' : 'Works__title'}`}>
              {data.name}
            </h2>
            <div className='Works__linkBlock'>
              <a
                className='Works__imgLink'
                href={data.link}
              >
                <img className='Works__img'
                src={data.img} />
              </a>
              <a
                className={`${isDarkTheme ? 'Works__link dark' : 'Works__link'}`}
                href={data.link}
              >
                {data.link}
              </a>
            </div>
          </div>
          )}
        </div>
      </motion.div>
    </AnimatePresence >
  )
}
