import React from 'react'
import { useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { appStateType } from '../../Redux/Store'

type PropsType = {}

export const Nav: React.FC<PropsType> = (props) => {
    const isDarkTheme = useSelector((state: appStateType) => state.ThemeReducer.darkTheme)

    return (
        <div className="Nav__nav">
            <ul className={`${isDarkTheme ? "Nav__list dark" : "Nav__list"}`}>
                <li className="Nav__li"><NavLink to='/' className="Nav__link"> </NavLink></li>
                <li className="Nav__li"><NavLink to='/works'className="Nav__link"> </NavLink></li>
            </ul>
        </div>
    )
}
