import React from 'react'
import './Footer.css'
import GitHubIcon from '@mui/icons-material/GitHub';
import { useSelector } from 'react-redux';
import { appStateType } from '../../Redux/Store';

export const Footer = () => {
    const isDarkTheme = useSelector((state: appStateType) => state.ThemeReducer.darkTheme)

    return (
        <footer className={`${isDarkTheme ? "Footer dark" : "Footer"}`}>
            <div className="Footer__wrapper">
                <a href='https://github.com/GitHubermane'><GitHubIcon className={`${isDarkTheme ? "Footer__githubIcon dark" : "Footer__githubIcon"}`}/></a>
            </div>
        </footer>
    )
}
